<template>
    <div class="mapForAddress" title="确认收货地址">
        <navigation-bar class="titleBar"  @onLeftClick="onLeftClick" :pageName="title" :bothWidth="headerItemWidth" ></navigation-bar>
        <div class="searchBar">
            <div class="cityBtnDiv" @click="changeCity">
                <div class="cityBtn">青岛</div>
                <img src="../../assets/images/common/home_cutover_black.png">
            </div>
            <van-search
                    id="tipinput"
                    v-model="inputAddress"
                    placeholder="小区/写字楼/学校等"
                    background="#F7F7F7"
                    shape="round"
                    @input="tipInputting"
                    style="float: right;padding:0;width: 75%;margin-right: 4%;margin-top: 1%"
            ></van-search>
        </div>
        <div class="mapDiv" v-if="!isNetworkError">
            <template>
                <div class="map">
                    <div id="GDMap" style="height:8rem">
                        <div class="shop-content-marker"></div>
                    </div>
                </div>
            </template>
        </div>
        <div class="addressListDiv">
            <div class="address-list" id="addressListDiv">
                <ul>
                    <li v-for="(item,i) in addressData">
                        <i class="node-icon">
                            <img src="../../assets/images/mall/gpsAddress.png" v-if="i == 0" />
                            <img src="../../assets/images/mall/gpsAddviceAddress.png" v-else/>
                        </i>
                        <div class="rightInfo">
                            <div>
                                <div id="name" class="addressName" style="color:#FB5768" v-if="i ==0">{{item.name}}</div>
                                <div class="addressName" style="color: #333333" v-else>{{item.name}}</div>
                            </div>
                            <div>
                                <div id="district" class="addressDetail" v-if="item.address.length !=0">{{item.address}}</div>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <div style="padding-top:100px;height:300px" v-if="isNetworkError ">
            <NetworkError></NetworkError>
        </div>
    </div>
</template>

<script>
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import NetworkError from '@/common/networkError/NetworkError';
    import loadMap from '../../components/AMapLoader/loadMap'
    export default {
        name: "mapForAddress.vue",
        components:{
            NavigationBar,
            NetworkError,
        },
        directives: {
            focus: {
                update: function (el, {value}) {
                    if (value) {
                        el.focus()
                    }
                }
            }
        },
        data(){
            return {
                title:'确认收货地址',
                headerItemWidth:{
                    width:'84px'
                },
                searchKey:"", //搜索的内容
                focusState:true, //焦点状态
                inputAddress:'',
                //地图实例
                expressMap:null,
                autoComplete:null,//地址输入联想
                isNetworkError:false,
                isBackSocketError:false,
                addressData:[],
            }
        },
        mounted() {
            loadMap()
                .then(AMap => {
                    this.expressMap = new AMap.Map('GDMap',{
                        zoom: 10,
                        center:[120.382689,36.066971],
                        animateEnable:true,
                        resizeEnable:true,
                    });
                    this.autoComplete = new AMap.Autocomplete({
                      city:'全国'
                    });
                })
                .catch((err) => {
                    this.isNetworkError=true;
                    console.log('地图加载失败 '+err)
                })
        },
        methods:{
            onLeftClick(){
                this.$router.go(-1);
            },
            //搜索输入框获取焦点
            searchInputFocus(e){
                var obj = e.srcElement;
                console.log("【输入框】searchInputFocus "+obj.value.length)
                var value = obj.value;
                obj.value = ""
                obj.value = value
            },
            tipInputting(val){
                let that = this;
                console.log(val);
                this.autoComplete.search(val, function(status, result) {
                    // 搜索成功时，result即是对应的匹配数据
                    console.log(result);
                    that.addressData=result.tips;
                })
            },
            //表单提交事件
            formSubmit () {
                return false
            },
            showCityView(){
                console.log("别催了别催了");
            },
            changeCity(){
                this.autoComplete.setCity('青岛');
            },
        }
    }
</script>

<style scoped lang="scss">
    .FixedsearchInput{
        width: 100%;
        border: 0px;
        background: #f7f7f7;
        height: 20px;
        line-height: 20px;
        padding: 0px;
        font-size:  12px;
        caret-color:#FD666A;
    }
    .searchBar{
        width: 345px;
        height: 38px;
        /*padding: 7px 15px;*/
        background: #F7F7F7;
        border-radius: 19px;
        line-height: 38px;
        margin-top: 7px;
        margin-left: 15px;
    }
    .cityBtnDiv{
        float: left;
        height: 25px;
        width: 64px;
        line-height: 25px;
        border-right: 1PX solid #D8D8D8;
        margin-top: 6px;
        margin-left: 5px;
        .cityBtn{
            font-family: PingFangSC-Medium;
            font-size: 16px;
            color: #333333;
            letter-spacing: 0;
            float: left;
            margin-left: 10px;
        }
        img{
            width: 6px;
            height: 4px;
            float: right;
            margin-top: 11px;
            margin-right: 10px;
        }
    }
    .mapDiv{
        margin-top: 7px;
    }
    .address-list{
        overflow-y: scroll;
        height: 250px;
    }
    .addressName{
        font-family: PingFangSC-Medium;
        font-size: 16px;
        width: 250px;
        padding: 0;
    }
    .addressDetail{
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #B2B2B2;
        text-align: left;
        height:20px;
        line-height: 20px;
    }
    .node-icon{
        float: left;
        margin-left: 15px;
        img{
            width: 8px;
        }
    }
    .rightInfo{
        width: 250px;
        height: 42px;
        margin-left: 33px;
        margin-top: 15px;
    }
</style>